html{font-size:10px}
body{background:#eee}
body > *{all:unset}

.container{width:98%;margin:0 auto;border-style:solid;border-width:1vw;border-image: linear-gradient(to right, #ff3066 0%, #7c4dff 100%);border-image-slice:1;background:#f7f7f7;box-sizing:border-box}
.loader{text-align:center}
.loader_txt{display:inline-block;padding:10rem;font-size:3rem;font-weight:bold;animation: loading 1s infinite}

@keyframes loading {
0% {
    background:-webkit-linear-gradient(-90deg, #ff3066, #7c4dff);
    -webkit-background-clip:text;
    -webkit-text-fill-color:transparent;

    -moz-background:linear-gradient(-90deg, #ff3066, #7c4dff);
    -moz-background-clip:text;
    -moz-text-fill-color:transparent;

    -o-background:linear-gradient(-90deg, #ff3066, #7c4dff);
    -o-background-clip:text;
    -o-text-fill-color:transparent;
}
50%{
    background:-webkit-linear-gradient(0deg, #ff3066, #7c4dff);
    -webkit-background-clip:text;
    -webkit-text-fill-color:transparent;

    -moz-background:linear-gradient(0deg, #ff3066, #7c4dff);
    -moz-background-clip:text;
    -moz-text-fill-color:transparent;

    -o-background:linear-gradient(0deg, #ff3066, #7c4dff);
    -o-background-clip:text;
    -o-text-fill-color:transparent;}
100% {
    background:-webkit-linear-gradient(90deg, #ff3066, #7c4dff);
    -webkit-background-clip:text;
    -webkit-text-fill-color:transparent;

    -moz-background:linear-gradient(90deg, #ff3066, #7c4dff);
    -moz-background-clip:text;
    -moz-text-fill-color:transparent;

    -o-background:linear-gradient(90deg, #ff3066, #7c4dff);
    -o-background-clip:text;
    -o-text-fill-color:transparent}
}